import tabs from "../../components/common/tabs/tabs.vue";
export default {
    components:{
        tabs
    },
    created() {
        this.getData()
    },
    data() {
        return {
            taskType:0,
            dytaskList: [],
            taskList: [],
            URL:getApp().globalData.URL,
        };
    },
    methods: {
        // tabs 监听
        changeTabIndex(index){
            // 0 抖音  1 普通任务
            this.taskType=index;
        },
        async getData() {
            await this.$u.get('/my_push_dy_task').then(res => {
                this.dytaskList = res.data
            })
            await this.$u.get('/my_push_task').then(res => {
                this.taskList = res.data
            })
        },
        del(id,type){
            //抖音
            if(type===1){
                uni.showModal({
                    title:"提示",
                    content:"确认下架？",
                    cancelText:"继续展示",
                    confirmText:"下架",
                    success:(res)=>{
                        if(res.confirm){
                            this.$u.post('/delete_dy_task',{dy_task_id:id}).then(res=>{
                                if(res.errorCode===0){
                                    uni.showToast({
                                        title:"下架成功"
                                    })
                                }
                                this.getData()
                            })
                        }
                        
                    }
                })
            }
            //悬赏
            if(type===2){
                uni.showModal({
                    title:"提示",
                    content:"确认下架？",
                    cancelText:"继续展示",
                    confirmText:"下架",
                    success:(res)=>{
                        if(res.confirm){
                            this.$u.post('/delete_task',{task_id:id}).then(res=>{
                                if(res.errorCode===0){
                                    uni.showToast({
                                        title:"下架成功"
                                    })
                                }
                                this.getData()
                            })
                        }
                        
                    }
                })
            }

        },
        open(id,type){
            if(type===1){
                this.$u.route('pages/myPushDyTaskOrder/myPushDyTaskOrder', {
                	dy_task_id:id
                });
            }
            if(type===2){
                this.$u.route('pages/myPushTaskOrder/myPushTaskOrder', {
                	task_id:id
                });
            }
          
        }
    }
}
